// vuex
// 
// 1.vuevuex
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';

// 2. 
Vue.use(Vuex);

// 3. Store
const store = new Vuex.Store({
    // 
    state:{
        topics:[
            {name:'all',desc:"",selected:true},
            {name:'good',desc:"",selected:false},
            {name:'share',desc:"",selected:false},
            {name:'ask',desc:"",selected:false},
            {name:'job',desc:"",selected:false},
            {name:'dev',desc:"",selected:false}
          ],
          // Actionsheet
          unLoginedMoreMenus:{
            login:''
          },
          // 
          loginedMoreMenus:{
            create:'',
            center:'',
            msg:'',
            logout:''
          },
          // 
          isLogined:false,  //
          // 
          loginname:'',
          // accesstoken
          accesstoken:'',
          // id
          author_id:'',
          // actionsheet
          isShowMoreMenus:false  // 
    },
    // 
    getters:{
        moreMenus:function(state){
            if(state.isLogined){
              // 
              return state.loginedMoreMenus;
            }
            return state.unLoginedMoreMenus;
        },
        getTopicDescByTab:function(state){
          return function(tab){
            for(var i=0;i<state.topics.length;i++){
              if(state.topics[i].name == tab){
                return state.topics[i].desc;
              }
            }
          }
        }
    },
    // 
    mutations,
    // 
    actions
})

// 4.
export default store;
// 5.main.jsstore


