export default {
    name:'center',
    // state
    // 
    computed:{
        loginname:function(){
            return this.$store.state.loginname;
        }
    },
    created:function(){
        //   
        // this.$router.pushhistory
        // replace,this.$router.replacehistory
        // 
        this.$router.replace('/user/'+this.loginname)
    }
}



